<template>
  <Toast />
  <transition-group name="p-message" tag="div">
      <Message v-for="msg of messages" :key="msg.id" :severity="msg.severity">{{ msg.content }}</Message>
  </transition-group>

  <form @submit.prevent="updateUser">

      <div class="flex flex-column">
          <div class="row mt-4">
              <h5 class="col-12">
                  
              </h5>
              <div class="col-5 mt-2 pt-2">
                  <label for="username"></label>
                  <InputText class="w-100" type="text" id="username" v-model="username" />
              </div>
              <div class="col-5 mt-2 pt-2">
                  <label for="firstName"> <span class="required">*</span></label>
                  <InputText class="w-100" type="text" id="firstName" v-model="firstName" required />
              </div>
              <div class="col-5 mt-2 pt-2">
                  <label for="lastName"> <span class="required">*</span></label>
                  <InputText class="w-100" type="text" id="lastName" v-model="lastName"  required/>
              </div>
              <div class="col-5 mt-2 pt-2">
                  <label for="email">Email <span class="required">*</span></label>
                  <InputText class="w-100" type="email" id="email" v-model="email" required />
              </div>
              <div class="col-5 mt-2 pt-2">
                  <label for="employeeId"></label>
                  <InputText class="w-100" type="text" id="employeeId" v-model="employeeId" disabled />
              </div>
              <div class="col-5 mt-2 pt-2">
                  <label for="position"></label>
                  <InputText class="w-100" type="text" id="position" v-model="position" disabled />
              </div>
              <div class="col-5 mt-2 pt-2">
                  <label for="department"></label>
                  <InputText class="w-100" type="text" id="department" v-model="department" disabled />
              </div>
          </div>
          <div class="row">
              <div class="col-1 ml-8">
                  <button type="submit" class="btn btn-success btn-block  mt-4"></button>
              </div>
          </div>
      </div>
  </form>
  <Divider class=" mt-3 pt-3" />
  <form>
      <div class="flex flex-column">
          <div class="row">
              <h5 class="col-12">
                  
              </h5>
              <div class="col-12 mt-3">
                  <div class="row">
                      <div class="col-12 col-md-4 mt-2">
                          <label for="currentPassword"></label>
                          <Password class="w-100" id="currentPassword" v-model="currentPassword" 
                                   toggle-mask :feedback="false" placeholder="" />
                      </div>
                      <div class="col-12 col-md-4 mt-2">
                          <label for="newPassword"></label>
                          <Password class="w-100" id="newPassword" v-model="newPassword" 
                                   toggle-mask :feedback="true" placeholder="" />
                      </div>
                      <div class="col-12 col-md-4 mt-2">
                          <label for="confirmPassword"></label>
                          <Password class="w-100" id="confirmPassword" v-model="confirmPassword" 
                                   toggle-mask :feedback="false" placeholder="" />
                      </div>
                  </div>
              </div>
          </div>
          <div class="mt-4">
              <Button type="button" @click="confirmChangePassword" 
                      :disabled="!canChangePassword" 
                      class="btn btn-danger btn-block">
                  
              </Button>
          </div>
      </div>
  </form>

</template>
<script setup>
import { ref, onMounted, computed } from "vue";
import { useRouter } from 'vue-router';
import { useToast } from 'primevue/usetoast';
import InputText from 'primevue/inputtext';
import Password from 'primevue/password';
import Message from 'primevue/message';
import Toast from "primevue/toast";
import { useConfirm } from "primevue/useconfirm";

const router = useRouter();
const toast = useToast();
const confirm = useConfirm();

// Loading state
const loading = ref(false);

// Computed properties
const canChangePassword = computed(() => {
  return currentPassword.value && 
         newPassword.value && 
         confirmPassword.value && 
         newPassword.value === confirmPassword.value &&
         newPassword.value.length >= 6;
});

onMounted(() => {
  fetchData();
});

// top message 
const messages = ref([]);

// data
var id = ref();
var username = ref();
var email = ref();
var firstName = ref();
var lastName = ref();
var employeeId = ref();
var role = ref();
var currentPassword = ref('');
var newPassword = ref();
var confirmPassword = ref('');
var position = ref();
var department = ref();
function fetchData() {
  const currentUserId = localStorage.getItem('soc_user_id');
  
  if (!currentUserId) {
    toast.add({
      severity: 'error',
      summary: '',
      detail: ' ',
      life: 3000
    });
    router.push('/login');
    return;
  }
  
  loading.value = true;
  
  // Use axios
  window.axios.get('/api/users')
    .then(response => {
      const users = response.data;
      const userData = users.find(user => user.id == currentUserId);
      
      if (!userData) {
        toast.add({
          severity: 'error',
          summary: '',
          detail: '',
          life: 3000
        });
        loading.value = false;
        return;
      }
      
      username.value = userData.username;
      email.value = userData.email;
      firstName.value = userData.firstname;
      lastName.value = userData.lastname;
      employeeId.value = userData.employee_id;
      role.value = { name: userData.role };
      position.value = userData.position;
      department.value = userData.department;
      id.value = userData.id;
      loading.value = false;
    })
    .catch(error => {
      console.error('Error fetching user data:', error);
      toast.add({
        severity: 'error',
        summary: '',
        detail: '',
        life: 3000
      });
      loading.value = false;
    });
}

function updateUser() {
  const data = {
    username: username.value,
    firstname: firstName.value,
    lastname: lastName.value,
    email: email.value,
    employee_id: employeeId.value || null,
    position: position.value || null,
    department: department.value || null,
    role: role.value?.name || 'user'
  };

  window.axios.put(`/api/users/${id.value}`, data, {
    headers: {
    }
  }).then(() => {
    toast.add({
      severity: 'success',
      summary: '',
      detail: '',
      life: 3000
    });
    
    localStorage.setItem('soc_firstname', firstName.value);
    localStorage.setItem('soc_lastname', lastName.value);
    localStorage.setItem('soc_position', position.value);
    localStorage.setItem('soc_department', department.value);
  }).catch(error => {
    toast.add({
      severity: 'error',
      summary: '',
      detail: error.response?.data?.error || '',
      life: 3000
    });
  });
}
function confirmChangePassword() {
  confirm.require({
      message: '?',
      header: '',
      icon: 'pi pi-exclamation-triangle',
      rejectClass: 'p-button-secondary p-button-outlined',
      rejectLabel: '',
      acceptLabel: '',
      accept: () => {
          changePassword();
      }
  });
}

function changePassword() {
  // Validation
  if (!currentPassword.value) {
    toast.add({ severity: 'error', summary: '', detail: '', life: 3000 });
    return;
  }
  
  if (!newPassword.value || newPassword.value.length < 6) {
    toast.add({ severity: 'error', summary: '', detail: ' 6 ', life: 3000 });
    return;
  }
  
  if (newPassword.value !== confirmPassword.value) {
    toast.add({ severity: 'error', summary: '', detail: '', life: 3000 });
    return;
  }
  
  var data = { 
    currentPassword: currentPassword.value,
    password: newPassword.value 
  };
  
  window.axios.put(`/api/users/${id.value}/password`, data, {
      headers: {
      }
  })
      // eslint-disable-next-line
      .then(changePass_res => {
          currentPassword.value = "";
          newPassword.value = "";
          confirmPassword.value = "";
          toast.add({ severity: 'success', summary: '', detail: '', life: 3000 });
      }).catch(changePass_err => {
          const errorMessage = changePass_err.response?.data?.error || '';
          toast.add({ severity: 'error', summary: '', detail: errorMessage, life: 3000 });
      });
}

</script>
<style></style>